'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import { toast } from 'sonner';
import { 
    Star, Minus, Plus, ShoppingCart, ArrowLeft, CheckCircle2, Clock, User, ShoppingBag, ArrowRight
} from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';

import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input'; 
import { Textarea } from '@/components/ui/textarea'; 
import { Separator } from '@/components/ui/separator';
import { ProdukResto } from '@/types';

export default function ProductDetailPage() {
    const params = useParams();
    const router = useRouter();
    const id = params.id;
    const { user } = useAuth({ middleware: 'auth' });

    const [product, setProduct] = useState<ProdukResto | null>(null);
    const [relatedProducts, setRelatedProducts] = useState<ProdukResto[]>([]);
    const [loading, setLoading] = useState(true);
    
    // State Transaksi
    const [quantity, setQuantity] = useState(1);
    const [catatan, setCatatan] = useState(''); 
    // Simulasi Table Number (seharusnya dari context/global state)
    const [tableNumber, setTableNumber] = useState(''); 

    // Fetch Data
    useEffect(() => {
        const fetchData = async () => {
            if (!id) return;
            try {
                setLoading(true);
                // 1. Ambil Detail Produk
                const resDetail = await axios.get(`/api/produkRestos/${id}`);
                setProduct(resDetail.data);

                // 2. Ambil Produk Lain untuk rekomendasi
                const resAll = await axios.get('/api/produkRestos');
                const others = resAll.data
                    .filter((p: ProdukResto) => p.id !== Number(id) && p.is_tersedia)
                    .slice(0, 3); // Ambil 3 saja agar muat di layout baru
                setRelatedProducts(others);

            } catch (error) {
                console.error(error);
                toast.error("Produk tidak ditemukan.");
                router.push('/pelanggan/produk');
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, [id, router]);

    const handleIncrement = () => setQuantity(prev => prev + 1);
    const handleDecrement = () => setQuantity(prev => (prev > 1 ? prev - 1 : 1));

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    const handleOrder = () => {
        toast.success("Berhasil ditambahkan ke pesanan!", {
            description: `${quantity}x ${product?.nama_produk} ${catatan ? `(${catatan})` : ''}`,
            icon: <CheckCircle2 className="text-green-600" />
        });
        // Redirect balik ke menu untuk lihat keranjang
        setTimeout(() => router.push('/pelanggan/produk'), 1000);
    };

    if (loading) return (
        <div className="min-h-screen bg-white flex items-center justify-center">
            <div className="animate-pulse flex flex-col items-center">
                <div className="h-12 w-12 bg-gray-200 rounded-full mb-4"></div>
                <div className="text-gray-400 font-medium">Memuat menu lezat...</div>
            </div>
        </div>
    );
    
    if (!product) return null;

    return (
        <div className="min-h-screen bg-[#f8f9fa] font-sans text-gray-900">
            <main className="max-w-7xl mx-auto px-4 md:px-6 py-8">
                
                {/* Header & Breadcrumb */}
                <div className="mb-6">
                    <Link href="/pelanggan/produk" className="inline-flex items-center text-gray-500 hover:text-[#D93F21] transition-colors font-medium group mb-4">
                        <ArrowLeft className="w-4 h-4 mr-2" /> Kembali ke Menu
                    </Link>
                    <h1 className="text-2xl md:text-3xl font-extrabold text-gray-900">
                        Detail <span className="text-[#D93F21]">Produk</span>
                    </h1>
                </div>

                <div className="flex flex-col lg:flex-row gap-8 items-start">
                    
                    {/* === BAGIAN KIRI: DETAIL PRODUK (65%) === */}
                    <div className="w-full lg:w-2/3 space-y-8">
                        
                        {/* Card Utama */}
                        <div className="bg-white rounded-[2rem] p-6 md:p-8 shadow-sm border border-gray-100">
                            <div className="flex flex-col md:flex-row gap-8">
                                {/* Gambar */}
                                <div className="w-full md:w-1/2 relative aspect-square rounded-2xl overflow-hidden bg-gray-100 border border-gray-100">
                                    <Image 
                                        src={product.foto_url || '/images/placeholder-food.jpg'} 
                                        alt={product.nama_produk}
                                        fill
                                        className="object-cover"
                                        unoptimized
                                    />
                                     <div className="absolute top-4 left-4">
                                        {product.is_tersedia ? (
                                            <Badge className="bg-green-500 hover:bg-green-600 px-3 py-1 text-sm shadow-sm border-none">Ready Stock</Badge>
                                        ) : (
                                            <Badge variant="destructive" className="px-3 py-1 text-sm shadow-sm">Sold Out</Badge>
                                        )}
                                    </div>
                                </div>

                                {/* Info */}
                                <div className="w-full md:w-1/2 flex flex-col justify-center">
                                    <div className="mb-2">
                                        <span className="text-[#D93F21] font-bold uppercase tracking-widest text-xs bg-[#D93F21]/10 px-3 py-1 rounded-full">
                                            {product.kategori?.nama_kategori}
                                        </span>
                                    </div>

                                    <h2 className="text-3xl font-extrabold text-gray-900 mb-2 leading-tight">
                                        {product.nama_produk}
                                    </h2>

                                    <div className="flex items-center gap-1 mb-4">
                                        {[1, 2, 3, 4, 5].map((star) => (
                                            <Star key={star} className="w-4 h-4 text-yellow-400 fill-yellow-400" />
                                        ))}
                                        <span className="text-gray-400 text-sm ml-2 font-medium">(4.8)</span>
                                    </div>

                                    <p className="text-gray-500 text-sm leading-relaxed mb-6">
                                        {product.deskripsi || "Menu spesial dengan cita rasa otentik khas MainDiHatta."}
                                    </p>

                                    <div className="h-px bg-gray-100 w-full mb-6"></div>

                                    <div className="mb-6">
                                        <label className="text-sm font-bold text-gray-900 mb-2 block">Catatan (Opsional)</label>
                                        <Textarea 
                                            placeholder="Cth: Pedas, Jangan pakai bawang..." 
                                            className="bg-gray-50 border-gray-200 focus:border-[#D93F21] rounded-xl resize-none h-20 text-sm"
                                            value={catatan}
                                            onChange={(e) => setCatatan(e.target.value)}
                                        />
                                    </div>

                                    <div className="flex items-center justify-between gap-4">
                                        <div className="flex items-center gap-3 bg-gray-50 p-1 rounded-full border border-gray-200">
                                            <button onClick={handleDecrement} className="w-8 h-8 rounded-full bg-white shadow-sm flex items-center justify-center hover:text-[#D93F21] transition-colors"><Minus className="w-4 h-4" /></button>
                                            <span className="text-lg font-bold text-gray-900 w-6 text-center">{quantity}</span>
                                            <button onClick={handleIncrement} className="w-8 h-8 rounded-full bg-white shadow-sm flex items-center justify-center hover:text-[#D93F21] transition-colors"><Plus className="w-4 h-4" /></button>
                                        </div>
                                        <p className="text-2xl font-extrabold text-[#D93F21]">
                                            {formatRupiah(Number(product.harga_jual) * quantity)}
                                        </p>
                                    </div>
                                    
                                    <Button 
                                        onClick={handleOrder}
                                        disabled={!product.is_tersedia}
                                        className="w-full mt-6 bg-[#D93F21] hover:bg-[#b9351b] text-white py-6 rounded-xl font-bold text-lg shadow-lg shadow-orange-200"
                                    >
                                        Add to Order <ShoppingCart className="ml-2 w-5 h-5" />
                                    </Button>
                                </div>
                            </div>
                        </div>

                        {/* Rekomendasi */}
                        <div>
                            <h3 className="text-xl font-bold text-gray-900 mb-4">Menu Lainnya</h3>
                            <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                                {relatedProducts.map((item) => (
                                    <Link key={item.id} href={`/pelanggan/produk/${item.id}`}>
                                        <div className="bg-white p-3 rounded-xl border border-gray-100 hover:shadow-lg transition-all cursor-pointer group">
                                            <div className="relative h-32 w-full rounded-lg overflow-hidden mb-3 bg-gray-100">
                                                <Image 
                                                    src={item.foto_url || '/images/placeholder-food.jpg'} 
                                                    alt={item.nama_produk}
                                                    fill
                                                    className="object-cover group-hover:scale-110 transition-transform duration-500"
                                                    unoptimized
                                                />
                                            </div>
                                            <h4 className="font-bold text-gray-900 text-sm line-clamp-1">{item.nama_produk}</h4>
                                            <div className="flex justify-between items-center mt-1">
                                                <p className="text-xs text-gray-500">{formatRupiah(Number(item.harga_jual))}</p>
                                                <ArrowRight className="w-4 h-4 text-[#D93F21]" />
                                            </div>
                                        </div>
                                    </Link>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* === BAGIAN KANAN: SIDEBAR INFO (35%) === */}
                    <div className="w-full lg:w-1/3">
                        <div className="sticky top-24 bg-white rounded-[2rem] shadow-xl border border-gray-100 overflow-hidden">
                            
                            {/* Customer Info (Sama seperti halaman Menu) */}
                            <div className="p-6 border-b border-gray-100">
                                <h3 className="text-[#D93F21] font-bold text-lg mb-4">Customer Information</h3>
                                <div className="space-y-3">
                                    <div>
                                        <p className="text-xs text-gray-400 font-medium mb-1">Customer Name :</p>
                                        <div className="flex items-center gap-2 font-bold text-gray-900 text-sm">
                                            <User className="w-4 h-4 text-gray-400" />
                                            {user?.name || 'Guest'}
                                        </div>
                                    </div>
                                    <div>
                                        <p className="text-xs text-gray-400 font-medium mb-1">Table Number :</p>
                                        <Input 
                                            type="number" 
                                            placeholder="No. Meja" 
                                            className="font-bold border-gray-200 focus:border-[#D93F21] rounded-lg bg-gray-50 h-10 text-sm"
                                            value={tableNumber}
                                            onChange={(e) => setTableNumber(e.target.value)}
                                        />
                                    </div>
                                </div>
                            </div>

                            {/* Placeholder Keranjang */}
                            <div className="p-8 text-center flex flex-col items-center justify-center text-gray-400 min-h-[250px] bg-gray-50">
                                <div className="w-14 h-14 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                                    <ShoppingBag className="w-7 h-7 text-gray-300" />
                                </div>
                                <h3 className="text-base font-bold text-gray-600 mb-2">Keranjang Anda</h3>
                                <p className="text-xs max-w-[200px] mb-4">Tambahkan produk ini ke pesanan untuk melihat ringkasan.</p>
                                <Link href="/pelanggan/produk">
                                    <Button variant="outline" size="sm" className="text-xs">
                                        Lihat Menu Lain
                                    </Button>
                                </Link>
                            </div>

                        </div>
                    </div>

                </div>
            </main>
        </div>
    );
}